import React from 'react';
import { Row, Col } from 'antd';
import { createCard1, createCard2, createCard3, createCard4, createCard, divider } from '../common';
import { 
  layer0_cdn, layer0_p2pm, layer0_vm, layer0_vm_c, layer0_vm_d, layer0_vm_eos, layer0_vm_utxo,
  layer1_cons_pow, layer1_cons_pos, layer1_cons_dpos, layer1_cons_poc,
  layer1_cons_poh, layer1_cons_poa, layer1_cons_tangle, layer1_cons_ava,
  layer1_cons_dbft, layer1_cons_pbft, layer1_cons_vbft, layer1_cons_lattice,
  layer1_data, layer1_data_pub, layer1_shard, layer1_inter,
  layer2_channels, layer2_plasma, layer2_storage_incent, layer2_side, layer2_rollup,
  layer2_cpu, layer2_oracle, layer2_geo, layer2_ilp, 
  layer2_pcc, layer2_ovc, layer2_es, layer2_pr, layer3_api, layer3_lang,
  layer4_content, layer4_game, layer4_dao, layer4_ad, layer4_msg, layer4_mkt, layer4_user,
  layer4_pay, layer4_lend, layer4_der, layer4_stable, layer4_syn, layer4_insur, layer4_ex, layer4_custo, layer4_pred,
} from '../../data/projects/web3'

const Web3Page = () => (
  <Col>
    {divider(' -> & -> Layer0')}
    <Row gutter={24}>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2('P2P -> CDN&BDN', layer0_cdn)}
      </Col>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2('P2P -> P2P', layer0_p2pm)}
      </Col>
      <Col xl={12} lg={24} md={24} sm={24} xs={24}>
        {createCard2('', layer0_vm)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={10} lg={24} md={24} sm={24} xs={24}>
        {createCard4(' -> ', layer0_vm_c)}
      </Col>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2(' -> Direct LLVM exposure', layer0_vm_d)}
      </Col>
      <Col xl={4} lg={24} md={24} sm={24} xs={24}>
        {createCard1(' -> WASM', layer0_vm_eos)}
      </Col>
      <Col xl={4} lg={24} md={24} sm={24} xs={24}>
        {createCard1(' -> UTXO', layer0_vm_utxo)}
      </Col>
    </Row>
    {divider(' ->  -> Layer1')}
    <Row gutter={24}>
      <Col xl={8} lg={24} md={24} sm={24} xs={24}>
        {createCard3(' -> PoW', layer1_cons_pow)}
      </Col>
      <Col xl={10} lg={24} md={24} sm={24} xs={24}>
        {createCard4('PoS', layer1_cons_pos)}
      </Col>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2('DPoS', layer1_cons_dpos)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2('PoC/PoST', layer1_cons_poc)}
      </Col>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>   
        {createCard2('PoA', layer1_cons_poa)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('PoH', layer1_cons_poh)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('PBFT', layer1_cons_pbft)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('DBFT', layer1_cons_dbft)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('VBFT', layer1_cons_vbft)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={15} lg={24} md={24} sm={24} xs={24}>
        {createCard('', layer1_data)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('DAG -> Tangle', layer1_cons_tangle)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('DAG -> Lattice', layer1_cons_lattice)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('Avalanche', layer1_cons_ava)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2('/', layer1_data_pub)}
      </Col>
      <Col xl={18} lg={24} md={24} sm={24} xs={24}>
        {createCard('', layer1_shard, '16.66%')}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={24} lg={24} md={24} sm={24} xs={24}>
        {createCard('', layer1_inter, '11.11%')}
      </Col>
    </Row>
    {divider(' ->  -> Layer2')}
    <Row gutter={24}>
      <Col xl={12} lg={24} md={24} sm={24} xs={24}>
        {createCard4('', layer2_channels)}
        {createCard4('Plasma', layer2_plasma)}
      </Col>
      <Col xl={12} lg={24} md={24} sm={24} xs={24}>
        {createCard4('', layer2_side)}
        {createCard4('Roll Up', layer2_rollup)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={12} lg={24} md={24} sm={24} xs={24}>
        {createCard4('', layer2_storage_incent)}
      </Col>
      <Col xl={12} lg={24} md={24} sm={24} xs={24}>
        {createCard4('Interledger', layer2_ilp)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={9} lg={24} md={24} sm={24} xs={24}>
        {createCard3('', layer2_oracle)}
      </Col>
      <Col xl={9} lg={24} md={24} sm={24} xs={24}>
        {createCard3('', layer2_cpu)}
      </Col>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2('', layer2_geo)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('', layer2_pcc)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('', layer2_ovc)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('', layer2_es)}
      </Col>
      <Col xl={3} lg={24} md={24} sm={24} xs={24}>
        {createCard1('', layer2_pr)}
      </Col>
    </Row>
    {divider(' ->  -> Layer3')}
    <Row gutter={24}>
      <Col xl={24} lg={24} md={24} sm={24} xs={24}>
        {createCard(' -> API', layer3_api, '12.5%')}
        {createCard(' -> ', layer3_lang, '12.5%')}
      </Col>
    </Row>
    {divider(' ->  -> Layer4')}
    <Row gutter={24}>
      <Col xl={24} lg={24} md={24} sm={24} xs={24}>
        {createCard('&', layer4_content, '12.5%')}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={12} lg={24} md={24} sm={24} xs={24}>
        {createCard4('', layer4_game)}
      </Col>
      <Col xl={12} lg={24} md={24} sm={24} xs={24}>
        {createCard4('', layer4_dao)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2('', layer4_ad)}
      </Col>
      <Col xl={6} lg={24} md={24} sm={24} xs={24}>
        {createCard2('', layer4_msg)}
      </Col>
      <Col xl={12} lg={24} md={24} sm={24} xs={24}>
        {createCard4('', layer4_mkt)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={24} lg={24} md={24} sm={24} xs={24}>
        {createCard(' -> DAPP', layer4_user, '12.5%')}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={9} lg={24} md={24} sm={24} xs={24}>
        {createCard3('', layer4_pay)}
        {createCard3('', layer4_der)}
        {createCard3('', layer4_syn)}
      </Col>
      <Col xl={15} lg={24} md={24} sm={24} xs={24}>
        {createCard('&', layer4_lend)}
        {createCard('', layer4_pred)}
        {createCard('', layer4_stable)}
      </Col>
    </Row>
    <Row gutter={24}>
      <Col xl={17} lg={24} md={24} sm={24} xs={24}>
        {createCard('&', layer4_ex, '16.66%')}
        {createCard('', layer4_custo, '16.66%')}
      </Col>
      <Col xl={7} lg={24} md={24} sm={24} xs={24}>
        {createCard2('', layer4_insur)}
      </Col>
    </Row>
  </Col>
)

export default Web3Page;